import React from 'react';
import Paragraph from '../../paragraph/paragraph';

type AppPartType = {
    icone?: string;
    bootomIcone?: string;
    titleLeft?: string;
    bootomTitleLeft?: string;
    paragraphRight?: string;
    paragraphLeft?: string;
    backgroundButton?: string;
    colorTitle?: string;
    buttonTitle?: string;
}
const AppPart = (props: AppPartType) => {
    return (
        <div className='space-y-12 w-full md:w-9/12 max-with1'>
            <div className='flex items-center justify-between'>
                <div className='space-y-2'>
                    <img className='-mr-20 h-14' src={props.icone} alt="" />
                    <Paragraph color='text-titlemenu' fonsize='text-xl md:text-2xl' title={props.bootomIcone} />
                </div>
                <div>
                    <Paragraph  color={props.colorTitle} fonsize='text-xl' title={props.titleLeft} />
                    <Paragraph  color='text-titlemenu' fonsize='text-lg' title={props.bootomTitleLeft} />
                </div>
            </div>
            <div className='flex flex-col md:flex-row items-center justify-between gap-6'>
                <Paragraph className='' color='text-titlemenu' fonsize='' title={props.paragraphRight} />
                <Paragraph className='' color='text-titlemenu' fonsize='' title={props.paragraphLeft} />
            </div>
            <div className='text-center mt-16'>
                <button style={{ background: props.backgroundButton }} className='text-white pt-2.5 pb-2.5 pr-8 pl-8 text-xl rounded-full button-box'>{props.buttonTitle}</button>
            </div>
        </div>
    );
};

export default AppPart;