import React from 'react';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
type model = {
    src: string;
}
type BoxsVideoType = {
    modle: model[];
}
const BoxsVideo = (props: BoxsVideoType) => {
    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 1,
        autoplay:true,
        responsive: [
            {
              breakpoint: 1024,
              settings: {
                slidesToShow: 3,
                slidesToScroll: 3,
                infinite: true,
                dots: true
              }
            },
            {
              breakpoint: 600,
              settings: {
                slidesToShow: 2,
                slidesToScroll: 2
              }
            },
            {
              breakpoint: 480,
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1
              }
            }
            // You can unslick at a given breakpoint now by adding:
            // settings: "unslick"
            // instead of a settings object
          ]
    };
    return (
        <div className='w-full'>
            <Slider {...settings}>
                {props.modle.map(item => {
                    return (
                        <div>
                            <img className='img-vidio' src={item.src} alt="" />
                        </div>
                    )
                })}
            </Slider>
        </div>
    );
};

export default BoxsVideo;