import React from 'react';
import Paragraph from '../paragraph/paragraph';
import MainTitle from '../title-paragraph/maintitle';
import Title from '../title/title.component';

const Row2 = () => {
    return (
        <div className='w-full space-y-6 md:space-y-12 pr-4 pl-4 md:w-2/5 mt-8 md:mt-24 m-auto'>
            <div>
            <MainTitle title2='از تیمی ۳ نفره تا خانواده‌ای چند صد نفره' title='داستان ما از کجا شروع شد؟'/>
            </div>
            <div>
            <Paragraph  color='color-paragraph' fonsize='text-base' title='در سال ۱۳۸۳، داستان صباایده از یک ایوان کوچک با ۳ جوان که سرمایه‌ای جز دانش و امید نداشتند شروع شد. نفوذ تدریجی اینترنت به خانه‌ها و فراهم شدن زیرساخت‌های لازم برای ارائۀ سرویس‌های اینترنتی، به تیم کوچک ما جهت و انگیزه می‌داد. از همان ابتدا می‌دانستیم مسیر پیش‌‌رو سخت است و ناهموار؛ بنابراین یاد گرفتیم امیدوارانه و در قالب یک تیم منسجم تلاش کنیم.' />
            <Paragraph  color='color-paragraph mt-6' fonsize='text-base' title='شکست‌ها و موفقیت‌ها، تلخی‌ها و شیرینی‌ها و خاطرات خوب و بد زیادی را تجربه کردیم تا امروز بتوانیم به محصولاتمان، و فراتر از آن به خانواده‌ی چند صد نفرۀ خود افتخار کنیم.' />
            </div> 
            <div className='text-center'>
            <button className='text-white pt-2 pb-2 pr-6 pl-6 text-xl rounded-full button-box'> آشنایی با محصولات ما</button>
            </div>
        </div>
    );
};

export default Row2;