import React from 'react';
import Title from '../title/title.component';

type Changetype = {
    img?:string;
    title?:string;
}
const Change = (props:Changetype) => {
    return (
        <div className='flex gap-4 w-full'>
            <div>
                <img className='rounded-lg image' src={props.img} alt="" />
            </div>
            <div className='flex flex-col justify-between'>
            <Title className='menu-title' color='text-titlemenu' fonsize='text-lg' title={props.title} />
            <button className='text-white w-24 h-9 rounded-full button-box'>بشتر بدانید</button>
            </div>
        </div>
    );
};

export default Change;