import React from 'react';

type model = {
    src1: string;
    src2: string;
}
type Page5Row3Type = {
    model: model[];
}
const Page5Row3 = (props: Page5Row3Type) => {
    return (
        <div className='flex gap-2.5'>
            {props.model.map(item => {
                return (
                    <div className='space-y-2.5 w-1/3'>
                        <img className='w-full' src={item.src1} alt="" />
                        <img className='w-full' src={item.src2} alt="" />
                    </div>
                )
            })}
        </div>
    );
};

export default Page5Row3;