import React from 'react';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';
import icone1 from '../../images/Emoticon/icons8-checked-24.png';
export type paragraphModel = {
    title: string;
    icone?: string;
}
export type model = {
    src: string;
    title: string;
    paragraph: paragraphModel[];
}
type ProfileTitleType = {
    modle: model[];
}
const ProfileTitle = (props: ProfileTitleType) => {
    return (
        <>
            {props.modle.map(item => {
                return (
                    <div className='flex gap-3 md:gap-6 w-full md:w-2/4 '>
                        <div>
                            <img className='h-20' src={item.src} alt="" />
                        </div>
                        <div className='space-y-6'>
                            <Title color='title-color' fonsize='text-2xl md:text-4xl' title={item.title} />
                            <div>
                                {item.paragraph.map(item => {
                                    return (
                                        <div className='flex gap-4 mb-1'>
                                          <div>  {item.icone && <img src={icone1} alt='' />}</div>
                                            <Paragraph color='color-paragraph' fonsize='text-base' title={item.title} />
                                        </div>
                                    )
                                })}
                            </div>
                        </div>
                    </div>
                )
            })}
        </>
    );
};

export default ProfileTitle;