import React from 'react';
import Paragraph from '../../paragraph/paragraph';

type ParagraphBoxsType = {
    paragraph1?: string;
    paragraraph2?: string;
}
const ParagraphBoxs = (props: ParagraphBoxsType) => {
    return (
        <div className='flex flex-col md:flex-row gap-8 pr-4 pl-4 justify-between'>
            <Paragraph color='text-titlemenu' title={props.paragraph1} />
            <Paragraph color='text-titlemenu' title={props.paragraraph2} />
        </div>
    );
};

export default ParagraphBoxs;