import React from 'react';
import icone1 from '../../images/Emoticon/bbc.png';
import icone2 from '../../images/Emoticon/cbcnews.png';
import icone3 from '../../images/Emoticon/aljazeera.png';
import icone4 from '../../images/Emoticon/economist.png';
import icone5 from '../../images/Emoticon/wsj.png';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
const LogoBoxs = () => {
    const img = [
        { src: icone1 },
        { src: icone2 },
        { src: icone3 },
        { src: icone4 },
        { src: icone5 },
    ];
    var settings = {
        centerMode: true,
        centerPadding: '60px',
        slidesToShow: 5,
        touchMove:false,
        responsive: [
            {
              breakpoint: 768,
              settings: {
                arrows: false,
                centerMode: true,
                centerPadding: '40px',
                slidesToShow: 3
              }
            },
            {
              breakpoint: 480,
              settings: {
                autoplay:true,
                autoplaySpeed:400,
                arrows: false,
                centerMode: true,
                centerPadding: '40px',
                slidesToShow: 2
              }
            }
          ]
      }
    return (
        <div className='w-11/12 pt-12 pb-12 container mx-auto'>
            <Slider {...settings}>
            {img.map(item => {
                return (
                    <div>
                    <img className='m-auto h-12 md:h-auto' src={item.src} alt="" />
                    </div>
                )
            })}
            </Slider>
        </div>
    );
};

export default LogoBoxs;