import React from 'react';
import ImageTitle, { imageTitle } from '../image-title/imagetitle';
import Title from '../title/title.component';
import icone1 from '../../images/Emoticon/book.svg';
import icone2 from '../../images/Emoticon/train.svg';
import icone3 from '../../images/Emoticon/lunch.svg';
import icone4 from '../../images/Emoticon/breakfast.svg';
import icone5 from '../../images/Emoticon/remote-working.svg';
import icone6 from '../../images/Emoticon/flexible-hours.svg';
import icone7 from '../../images/Emoticon/prize.svg';
import icone8 from '../../images/Emoticon/cake.svg';
import icone9 from '../../images/Emoticon/welfare-facilities.svg';
import icone10 from '../../images/Emoticon/tent.svg';
import icone11 from '../../images/Emoticon/football.svg';
import icone12 from '../../images/Emoticon/insurance.svg';
import MainTitle from '../title-paragraph/maintitle';
const Row5 = () => {
    const data = [
        { title: 'کتابخانه', icone: icone1 },
        { title: 'نزدیکی به وسایل حمل و نقل', icone: icone2 },
        { title: 'ناهار', icone: icone3 },
        { title: 'صبحانه', icone: icone4 },
    ];
    const data2 = [
        { title: 'دورکاری', icone: icone5 },
        { title: 'ساعات کاری منعطف', icone: icone6 },
        { title: 'پاداش عملکرد', icone: icone7 },
        { title: 'جشن‌های تولد', icone: icone8 },
    ];
    const data3 = [
        { title: 'تسهیلات رفاهی', icone: icone9 },
        { title: 'اردوهای تیمی', icone: icone10 },
        { title: 'تسهیلات ورزشی', icone: icone11 },
        { title: 'بیمه تکمیلی', icone: icone12 },
    ];
    const repate = (data: imageTitle[]) => {
        return (
            <div className='grid grid-cols-2  justify-items-center gap-8 md:grid-cols-4 '>
                <ImageTitle modle={data} />
            </div>
        )
    }
    return (
        <div className='mt-12 container  mx-auto'>
            <MainTitle title2='از تیمی ۳ نفره تا خانواده‌ای چند صد نفره' title='جذابیت‌های کار در صباایده'/>
            <div className='space-y-8 mt-14 w-full md:w-4/5 m-auto'>
                {repate(data)}
                {repate(data2)}
                {repate(data3)}
            </div>
            <div className='text-center mt-16'>
            <button className="text-white pt-2.5 pb-2.5 pr-8 pl-8 text-xl rounded-full button-box"> به ما بپیوندید</button>
            </div>
        </div>
    );
};

export default Row5;