import React from 'react';
import Title from '../title/title.component';

type MainTitleType = {
    title?: string;
    title2?: string;
}
const MainTitle = (props: MainTitleType) => {
    return (
        <>
            <Title textalign='text-center' className='menu-title' color='title-color' fonsize='text-2xl md:text-3xl' title={props.title} />
            <Title textalign='text-center' className='color-paragraph mt-2' color='title-color' fonsize='text-xl' title={props.title2} />
        </>
    );
};

export default MainTitle;