import React from 'react';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';

export type modelBoxsImage = {
    image?: string;
    title?: string;
    paragraph?: string;
    data?: string;
}
type boxsImageType = {
    mddel: modelBoxsImage[];
}
const BoxsImage = (props: boxsImageType) => {
    return (
        <>
            {props.mddel.map(item => {
                return (
                    <a className='w-full md:w-4/12 space-y-6' href='#'>
                            <div className='iamge-boxs rounded-md'>
                                <img src={item.image} alt="" />
                            </div>
                            <Title color='title-color-image' fonsize='text-2xl' title={item.title} />
                            <Paragraph color='color-paragraph' fonsize='text-base' title={item.paragraph} />
                            <div className='flex justify-between'>
                                <a href="#"> <Paragraph className='menu-title' color='title-color' fonsize='text-base' title='بیشتر بخوانید' /></a>
                                <Paragraph className='menu-title' color='color-paragraph' fonsize='text-base' title={item.data} />
                            </div>
                    </a>
                )
            })}
        </>
    );
};

export default BoxsImage;