import React from 'react';
import Menu from '../component/menu/menu.component';
import Page5Row2 from '../component/page5component/page5row2/page5row2';
import Page5Row3 from '../component/page5component/page5row3/page5row3';
import icone1 from '../images/Emoticon/bg (2).jpg';
import icone2 from '../images/Emoticon/3.jpg';
import icone3 from '../images/Emoticon/6.jpg';
import icone4 from '../images/Emoticon/2.jpg';
import icone5 from '../images/Emoticon/5.jpg';
import icone6 from '../images/Emoticon/1.jpg';
import icone7 from '../images/Emoticon/4.jpg';
import Paragraph from '../component/paragraph/paragraph';
import Page5Row4 from '../component/page5component/page5row4/page5row4';
import Footer from '../component/footer/footer.component';

const Page5 = () => {
    const menu = [
        { title: 'محصولات', link: '/prodoct' },
        { title: 'تایم لاین', link: '/about' },
        { title: 'در رسانه ها', link: '/press' },
        { title: 'سبک زندگی', link: '/life' },
        { title: 'همکاری', link: '/job' },
        { title: ' تماس', link: 'prodoct' },
    ];
    const images = [
        { src1: icone2, src2: icone3 },
        { src1: icone4, src2: icone5 },
        { src1: icone6, src2: icone7 },
    ]
    return (
        <div>
            <div className='max-with1'>
                <Menu model={menu} />
            </div>
            <div>
                <img className='w-full' src={icone1} alt="" />
            </div>
            <div className='mt-16'>
                <Page5Row2 />
            </div>
            <div className='mt-16'>
                <Page5Row3 model={images} />
                <div className='text-center mt-16 space-y-6'>
                    <Paragraph className='' color='text-titlemenu' fonsize='text-xl' title='نگاهی نزدیک تر به زندگی در صباایده' />
                    <button className='text-white pt-2.5 pb-2.5 pr-8 pl-8 text-xl rounded-full button-box insatgram-buuton'>اینستاگرام صبا ایده</button>
                </div>
            </div>
            <div className='background mt-16 pt-16'>
                <Page5Row4 />
            </div>
            <div className='mt-32'>
                <Footer />
            </div>
        </div>
    );
};

export default Page5;