import React from 'react';
import Footer from '../component/footer/footer.component';
import LogoBoxs from '../component/logo-boxs/logo-boxs';
import Menu from '../component/menu/menu.component';
import Page4Row2 from '../component/page4component/page4row2/page4row2';
import Page4Row3 from '../component/page4component/page4row3/page4row3';
import icone1 from '../images/Emoticon/bg (2).jpg';

const Page4 = () => {
    const menu = [
        { title: 'محصولات', link: '/prodoct' },
        { title: 'تایم لاین', link: '/about' },
        { title: 'در رسانه ها', link: '/press' },
        { title: 'سبک زندگی', link: '/life' },
        { title: 'همکاری', link: '/job' },
        { title: ' تماس', link: 'prodoct' },
    ];
    return (
        <div>
            <div className='max-with1'>
                <Menu model={menu} />
            </div>
            <div>
                <img className='w-full' src={icone1} alt="" />
            </div>
            <div className='mt-16'>
                <Page4Row2 />
            </div>
            <div className='background mt-16 pt-16'>
                <Page4Row3/>
            </div>
            <div className='background-logo-box mt-16'>
                <LogoBoxs />
            </div>
            <div className='mt-32'>
                <Footer />
            </div>
        </div>
    );
};

export default Page4;