import React from 'react';
import icone1 from '../../images/Emoticon/sabaidea-logo (1).svg';
import ItemFooter from '../item-footer/item-footer';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';
const Footer = () => {

    return (
        <div className='pr-4 w-full md:w-4/5 max-with1 space-y-6 md:space-y-12  mb-12 pl-4'>
            <div>
                <img className='h-8 m-auto' src={icone1} alt="" />
                <Title textalign='text-center' className='color-paragraph mt-2' color='title-color' fonsize='text-sm' title='پیشرو در صنعت کسب‌و‌کار‌های فناورانه' />
            </div>
            <ItemFooter/>
            <div className='text-center'>
                <button className="text-white pt-2.5 pb-2.5 pr-8 pl-8 text-xl rounded-full button-box">تماس با صباایده</button>
            </div>
            <Paragraph textalign='text-center' className='color-paragraph' color='title-color' title='تمام حقوق مادی و معنوی این وب‌سایت برای صباایده محفوظ است.' />
        </div>
    );
};

export default Footer;