import React from 'react';

type model = {
    src: string;
}
type LogoImageType = {
    model: model[];
    size?:string;
}
const LogoImage = (props: LogoImageType) => {
    return (
        <>
            {props.model.map(item => {
                return (
                    <a href='#'><img className={props.size} src={item.src} alt="" /></a>
                )
            })}
        </>
    );
};

export default LogoImage;