import React from 'react';
import ImageTitle, { imageTitle } from '../../image-title/imagetitle';
import icone1 from '../../../images/Emoticon/teamwork.svg';
import icone2 from '../../../images/Emoticon/plant.svg';
import icone3 from '../../../images/Emoticon/training.svg';
import icone5 from '../../../images/Emoticon/entertainment.svg';
import icone6 from '../../../images/Emoticon/freindly.svg';
import icone7 from '../../../images/Emoticon/money.svg';
import MainTitle from '../../title-paragraph/maintitle';
import Paragraph from '../../paragraph/paragraph';
const Page5Row4 = () => {
    const data = [
        { title: 'تیم های کاری پویا ایده ها را به اجرا در می آوردند.', icone: icone1, title1: 'کار تیمی' },
        { title: 'سعی مان این است تامحیط کارمان سرسبز باشد', icone: icone2, title1: 'محیط کار' },
        { title: 'برای اعضای خانواده ی صباایدهکلاس های آموزشی برگزار می کنیم', icone: icone3, title1: 'آموزش همکاران' },
    ];
    const data2 = [
        { title: 'چندین بار در طول سال تفریحات خارج از محیط کار داریم', icone: icone5, title1: 'تفریح و سرگرمی' },
        { title: 'نمی توان گفت با هم همکاریم!     بدون اغراق بیشتر رفیقیم تا همکار', icone: icone6, title1: 'رفاقت و دوستی' },
        { title: 'تلاش میکنیم جبران خدمات منصفانه ای برای تیم مهیا کنیم', icone: icone7, title1: 'حقوق و مزایای مناسب' },
    ];
    const repate = (data: imageTitle[]) => {
        return (
            <div className='grid grid-cols-1 justify-items-center gap-20 md:grid-cols-3 '>
                <ImageTitle modle={data} />
            </div>
        )
    }
    return (
        <div className=' container space-y-14 pr-4 pl-4 mx-auto'>
            <div>
                <MainTitle title2='می‌خواهیم به کمک هم بهترین باشیم' title='چرا صباایده؟' />
            </div>
            <Paragraph textalign='text-center' className='color-paragraph mt-2 w-full md:w-3/5 m-auto' color='title-color' title='صبا ایده شرکتی است فعال در حوزه فناوری های اینترنتی که تعدادی از پر ترافیک ترین وب سایت های ایرانی از جمله آپارات، کلوب، میهن بلاگ، لنزور و فیلیمو را خلق کرده است. همچنین صباایده به عنوان مالک آژانس تبلیغاتی صباویژن، در زمینه تبلیغات اینترنتی و خدمات بازاریابی دیجیتال نیز حضور پر رنگی دارد.' />
            <div className='space-y-8 w-full md:w-4/5 m-auto'>
                {repate(data)}
                {repate(data2)}
            </div>
            <div className='text-center mt-16'>
                <button className="text-white pt-2.5 pb-2.5 pr-8 pl-8 text-xl rounded-full button-box"> موقعیت‌های شغلی</button>
            </div>
        </div>
    );
};

export default Page5Row4;