import React from 'react';
import Paragraph from '../paragraph/paragraph';
import icone2 from '../../images/Emoticon/icons8-aparat-24.png';
import icone3 from '../../images/Emoticon/icons8-linkedin-24.png';
import icone4 from '../../images/Emoticon/icons8-instagram-24.png';
import icone5 from '../../images/Emoticon/icons8-twitter-24.png';
const ItemFooter = () => {
    const repatParagraph = (str1: string, str2: string) => {
        return (
            <div>
                <Paragraph textalign='text-center' className='color-paragraph mt-2' color='title-color' fonsize='text-xl' title={str1} />
                <Paragraph textalign='text-center' className='color-paragraph mt-2' color='title-color' fonsize='text-xl' title={str2} />
            </div>
        )
    }
    const image = [
        { src: icone2 },
        { src: icone3 },
        { src: icone4 },
        { src: icone5 },
    ]
    return (
        <div className='flex gap-4 md:gap-12 justify-center flex-col md:flex-row'>
            {repatParagraph('تهران، بزرگراه مدرس، خیابان وحید', 'دستگردی (ظفر شرقی)، پلاک ۲۱۷')}
            <div className='w-16 h-0.5 m-auto md:w-0.5 md:h-16 background-logo'></div>
            {repatParagraph('تلفن ۰۲۱ ۲۲۲۲ ۲۱ ۳۶', 'ایمیل info@sabaidea.com')}
            <div className='w-16 h-0.5 m-auto md:w-0.5 md:h-16 background-logo'></div>
            <div className='flex items-center justify-center gap-4'>
                {image.map(item => {
                    return (
                        <a href='#' className='rounded-full hover:bg-gray-400 h-12 w-12 flex items-center justify-center background-logo'>
                            <img src={item.src} alt="" />
                        </a>
                    )
                })}
            </div>
        </div>
    );
};

export default ItemFooter;