import React from 'react';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import icone1 from '../../images/Emoticon/sabaidea10_lg.jpg';
import icone2 from '../../images/Emoticon/sabaidea12_lg.jpg';
import icone3 from '../../images/Emoticon/sabaidea11_lg.jpg';
import icone4 from '../../images/Emoticon/sabaidea13_lg.jpg';
const Row4 = () => {
    var settings = {
        autoplay:true,
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
    };
 
    return (
        <div className='w-full overflow-hidden mt-12'>
            <Slider {...settings}>
               <div>
                <img className='w-full' src={icone1} alt="" />
               </div>
               <div>
                <img className='w-full' src={icone2} alt="" />
               </div>
               <div>
                <img className='w-full' src={icone3} alt="" />
               </div>
               <div>
                <img className='w-full' src={icone4} alt="" />
               </div>
            </Slider>
        </div>
    );
};

export default Row4;