import React from 'react';
import Paragraph from '../../paragraph/paragraph';
import Title from '../../title/title.component';

type model = {
    icone?: string;
    title?: string;
    paragraph?: string;
    line?: string;
}
type BoxsTitleType = {
    model: model[];
    textColor?:string;
}
const BoxsTitle = (props: BoxsTitleType) => {
    return (
        <div className='pt-12 max-with1 items-center pb-12 w-full grid grid-cols-2 md:w-9/12 m-auto md:flex gap-6 md:justify-center'>
            {props.model.map(item => {
                const className = 'w-px h-32 bg-white' + ' ' + item.line;
                return (
                    <div className='flex gap-6 md:gap-12 mb-4'>
                        {item.line && <div className={className}></div>}
                        <div className='space-y-1'>
                            <div><img className='m-auto' src={item.icone} alt="" /></div>
                            <Title textalign='text-center' color={props.textColor} fonsize='text-xl md:text-3xl' title={item.title} />
                            <Paragraph textalign='text-center' color={props.textColor}fonsize='text-lg' title={item.paragraph} />
                        </div>
                    </div>
                )
            })}
        </div>
    );
};

export default BoxsTitle;