import React from 'react';
import Boxs from '../component/boxs/boxs.componet';
import LogoImage from '../component/logo-image/logo-image';
import Menu from '../component/menu/menu.component';
import Row2 from '../component/row2/row2.component';
import Title from '../component/title/title.component';
import icone1 from '../images/Emoticon/aparat-logo.svg';
import icone2 from '../images/Emoticon/filimo-logo.svg';
import icone3 from '../images/Emoticon/cinematicket-logo.svg';
import icone4 from '../images/Emoticon/sabavision-logo.svg';
import icone5 from '../images/Emoticon/filimoschool-vertical-fa.svg';
import icone6 from '../images/Emoticon/aparatkids-vertical-fa-v2.svg';
import Row3 from '../component/row3/row3';
import Row4 from '../component/row4/row4.component';
import Row5 from '../component/row5/row5';
import Row6 from '../component/row6/row6';
import Row7 from '../component/row7/row7';
import Row8 from '../component/row8/row8';
import LogoBoxs from '../component/logo-boxs/logo-boxs';
import Footer from '../component/footer/footer.component';
import TitleRow3 from '../component/title-row3/title-row3';
const Page1 = () => {
    const menu = [
        { title: 'محصولات',link:'/prodoct' },
        { title: 'تایم لاین',link:'/about' },
        { title: 'در رسانه ها',link:'/press'  },
        { title: 'سبک زندگی',link:'/life'  },
        { title: 'همکاری',link:'/job'  },
        { title: ' تماس',link:'prodoct'  },
    ]
    const images = [
        { src: icone1 },
        { src: icone2 },
        { src: icone3 },
        { src: icone4 },
        { src: icone5 },
        { src: icone6 },
    ];
    return (
        <div>
            <div className='max-with1 '>
                <Menu model={menu} />
            </div>
            <div className='w-full relative myVideo '>
                <video autoPlay loop muted>
                    <source src="https://www.sabaidea.com/media/video/sabaidea.mp4?6" type="video/mp4" />
                </video>
                <div className='absolute space-y-6 left-0 right-0 ml-auto mr-auto  w-full p-3 max-with1 bottom-0 md:bottom-20'>
                    <Title color='text-white' fonsize='text-4xl' title='۱۵ سال' />
                    <Title color='text-white' fonsize='text-4xl' title='امیدوارانه در مسیر پیشرفت تلاش کرده‌ایم' />
                    <Boxs />
                </div>
            </div>
            <div className='flex md:hidden justify-center gap-8 mt-10 flex-wrap'>
                <LogoImage size='h-20' model={images} />
            </div>
            <div className='max-with1 pb-12'>
                <Row2 />
            </div>
            <div className='button-box pt-12 pb-48'>
                <TitleRow3/>
            </div>
            <div className='-mt-28'>
                <Row3 />
            </div>
            <div>
                <Row4 />
            </div>
            <div className='max-with1'>
                <Row5 />
            </div>
            <div className='background mt-16 pt-16'>
                <Row6 />
            </div>
            <div>
                <Row7 />
            </div>
            <div className='mt-10'>
                <Row8 />
            </div>
            <div className='background-logo-box mt-16'>
                <LogoBoxs />
            </div>
            <div className='mt-32'>
                <Footer />
            </div>
        </div>
    );
};

export default Page1;