import React from 'react';
import icone1 from '../../images/Emoticon/aparat-logo.svg';
import icone2 from '../../images/Emoticon/filimo-logo.svg';
import icone3 from '../../images/Emoticon/cinematicket-logo.svg';
import icone4 from '../../images/Emoticon/sabavision-logo.svg';
import icone5 from '../../images/Emoticon/filimoschool-vertical-fa.svg';
import icone6 from '../../images/Emoticon/aparatkids-vertical-fa-v2.svg';
import icone7 from '../../images/Emoticon/aparat.jpg';
import Change from '../change/change.component';
import LogoImage from '../logo-image/logo-image';

const Boxs = () => {
    const images = [
        { src: icone1 },
        { src: icone2 },
        { src: icone3 },
        { src: icone4 },
        { src: icone5 },
        { src: icone6 },
    ];
    const slider = [
        { img: icone7, title: "دورکاری؛ رنج یا لذت؟" }
    ]
    return (
        <div className='bg-white flex items-center gap-6 rounded-md p-2 md:p-7'>
            <div className='hidden md:flex gap-12'>
                <LogoImage size='h-24' model={images} />
            </div>
            <div className='hidden md:block w-0.5 bg-gray-300'></div>
            <div>
                <Change img={icone7} title="دورکاری؛ رنج یا لذت؟" />
            </div>
        </div>
    );
};

export default Boxs;