import React from 'react';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';

export type imageTitle = {
    title?: string;
    icone?: string;
    title1?: string;
}
type ImageTitleType = {
    modle: imageTitle[]
}
const ImageTitle = (props: ImageTitleType) => {
    return (
        <>
            {props.modle.map(item => {
                return (
                    <a href='#'>
                        <img className='image-title m-auto' src={item.icone} alt="" />
                        {item.title1 && <Title  textalign='text-center' className='menu-title mt-3' color='title-color' fonsize='text-xl' title={item.title1} />}
                        <Paragraph className='mt-3' textalign='text-center' color='color-paragraph' fonsize='text-base' title={item.title} />
                    </a>
                )
            })}
        </>
    );
};

export default ImageTitle;