import React from 'react';
import ProfileTitle, { model, paragraphModel } from '../profile-title/profile.title';
import icone1 from '../../images/Emoticon/vision.svg';
import icone2 from '../../images/Emoticon/mission.svg';
import icone3 from '../../images/Emoticon/values.svg';
import icone4 from '../../images/Emoticon/targets.svg';

const paragraph = [
    { title: 'خلق بهترین تجربه کاربری و ارزش آفرینی در' },
    { title: 'کسب‌وکارهای فناورانه' },
]
const paragraph2 = [
    { title: 'ارائه خدمات مفید و پایدار فناورانه مبتنی بر توسعه' },
    { title: 'سرویس‌های پرکاربر' },
]
const paragraph3 = [
    { title: 'خوش بینی و بلندپروازی', icone: 'true' },
    { title: 'سخت کوشی و روحیه تیمی', icone: 'true' },
    { title: 'صراحت و انصاف', icone: 'true' },
    { title: 'انجام کار درست', icone: 'true' },
]
const paragraph4 = [
    { title: 'خدمات باکیفیت و محبوب', icone: 'true' },
    { title: 'چابک با فضای دوستانه', icone: 'true' },
    { title: 'ارزش افزوده حداکثری', icone: 'true' },
    { title: 'حضور فعال در عرصه بین‌الملل', icone: 'true' },
]
const data: model[] = [
    { src: icone1, title: 'چشم‌انداز', paragraph: paragraph },
    { src: icone2, title: 'مأموریت', paragraph: paragraph2 },
]
const data2: model[] = [
    { src: icone3, title: 'ارزش‌ها', paragraph: paragraph3 },
    { src: icone4, title: 'اهداف', paragraph: paragraph4, },
]
const repeat = (data: model[], str: string) => {
    const className = ('bg-white m-auto p-4 md:p-10 flex flex-col md:flex-row space-y-3 md:space-y-0 md:rounded-2xl') + ' ' + str
    return (
        <div className={className}>
            <ProfileTitle modle={data} />
        </div>
    )
}
const Row3 = () => {
    return (
        <div className='md:space-y-3 mt-12 max-with1'>
            {repeat(data, 'shadow-lg')}
            {repeat(data2, '')}
        </div>
    );
};

export default Row3;