import React from 'react';
import LogoImage from '../../logo-image/logo-image';
import MainTitle from '../../title-paragraph/maintitle';
import icone1 from '../../../images/Emoticon/aparat-logo.svg';
import icone2 from '../../../images/Emoticon/filimo-logo.svg';
import icone3 from '../../../images/Emoticon/cinematicket-logo.svg';
import icone4 from '../../../images/Emoticon/sabavision-logo.svg';
import icone5 from '../../../images/Emoticon/filimoschool-vertical-fa.svg';
import icone6 from '../../../images/Emoticon/aparatkids-vertical-fa-v2.svg';

const Page2Row2 = () => {
    const images = [
        { src: icone1 },
        { src: icone2 },
        { src: icone3 },
        { src: icone4 },
        { src: icone5 },
        { src: icone6 },
    ];
    return (
        <div className='space-y-14 container mx-auto max-with1'>
            <div>
                <MainTitle title2='از پرترافیک ترین سرویس های آنلاین ایرانی' title='آشنایی با محصولات اصلی صباایده' />
            </div>
            <div className='flex gap-20 flex-wrap justify-center'>
                <LogoImage size='h-24' model={images} />
            </div>
            <div className='text-center mt-16'>
                <button className="text-white pt-2.5 pb-2.5 pr-8 pl-8 text-xl rounded-full button-box">بیشتر بدانید</button>
            </div>
        </div>
    );
};

export default Page2Row2;