import React from 'react';
import icone1 from '../../images/Emoticon/icons8-home-24.png';
import icone2 from '../../images/Emoticon/sabaidea-logo.svg';
import icone3 from '../../images/Emoticon/icons8-menu-24.png';
import Paragraph from '../paragraph/paragraph';
import { Link } from 'react-router-dom';

type model = {
    title?: string;
    link: string;
}
type MenuType = {
    model: model[];
}
const Menu = (props: MenuType) => {

    return (
        <div className='pt-5 items-center pb-5 pr-2 pl-2 flex justify-between'>
            <div className='flex gap-6'>
                <div className='hidden md:block'>
                    <Link to='/'>  <img src={icone1} alt="" /></Link>
                </div>
                <div className='hidden md:flex gap-8'>
                    {props.model.map(item => {
                        return (
                            <Link className='menu-title' to={item.link}> <Paragraph className='menu-title' color='text-titlemenu' fonsize='text-xl' title={item.title} /></Link>
                        )
                    })}
                </div>
                <div className='md:hidden'>
                    <img className='h-10' src={icone3} alt="" />
                </div>
                <a className='hidden md:block' href='#'> <div className='change-languige pr-0.5 pl-0.5 w-6 flex items-center justify-center h-6 rounded-md text-white'>EN</div></a>
            </div>
            <div>
                <img className='h-10' src={icone2} alt="" />
            </div>
        </div>
    );
};

export default Menu;